import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Div,  platform, IOS, Group} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const OilFilter = props => (
	<Panel id={props.id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Масляный фильтр
      </PanelHeader>

    <Group>
     <Div>
      <center>Масляный фильтр</center>
          <p>Масляный фильтр относится к сменным элементам автомобильного двигателя, от которых зависит эффективность работы мотора, долговечность его эксплуатации. Регулярная замена масляного фильтра входит в эксплуатационные правила, поэтому грамотный автолюбитель должен разбираться в конструкции узла, уметь выбрать качественный фильтр.</p>
		  <p>Герметичность автомобильного двигателя, практически полностью заполненного маслом, не гарантирует постоянной чистоты смазки. Двумя главными причинами загрязнения масла становятся механические примеси (микростружка от выработки собственных частей двигателя), продукты неполного сгорания топлива. При этом загрязнение сажей более характерно для дизельных двигателей, а процент механических примесей непосредственно связан с возрастом (пробегом) автомобиля, износом мотора.</p>
		  <p>Основные функции моторного масла (эффективную смазку, очистку рабочих поверхностей, охлаждение) загрязненная рабочая жидкость исполнять не может. Работа на грязном масле губительна для мотора, ведет к выходу его из строя. Для защиты от примесей, постоянной очистки масла применяется масляный фильтр</p>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Конструкция и типы</center>
		  <p>В конструкциях автодвигателей применяется три основных системы очистки. Наиболее часто в легковых автомобилях применяется полнопоточный масляный фильтр. В нем фильтрующий элемент включен в основную масляную линию. При каждом запуске двигателя все масло проходит через фильтр под действием масляного насоса.</p>
		  <p>Реже применяются конструкции с частичнопоточным масляным фильтром, подключаемым к масляной линии параллельно. Такой фильтр пропускает фильтруемое масло по частям, добиваясь чистоты при длительной работе. Конструкции комбинированных фильтров совмещают оба устройства, применяясь в мощных дизельных двигателях грузовиков.</p>
		  <p>Рядовому автолюбителю не обязательно знать конструктивные отличия своей машины по фильтрации автомобильного масла. Достаточно знать, что в большинстве легковых моделей применяются полнопоточные масляные фильтры, понимать их устройство, от элементов которого зависит очищающая способность узла.</p>
     </Div>
    </Group>
	</Panel>
);

OilFilter.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default OilFilter;
