import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Div,  platform, IOS, Group} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const AirFilter = props => (
	<Panel id={props.id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Воздушный фильтр
      </PanelHeader>

    <Group>
     <Div>
      <center>Воздушный фильтр</center>
        <p>Устройство современного автомобиля таково, что мелочи в нем отсутствуют. Даже те детали, которые на первый взгляд не особо важны, играют важную роль. К примеру, воздушный фильтр, значение которого автомобилистами часто преуменьшается. На самом деле, его значение огромно, ведь если он отсутствует или сильно загрязнен, нормальный процесс сгорания топлива становится невозможным, а количество вредных веществ, содержащихся в выхлопных газах будет резко увеличено.</p>
        <p>Для обеспечения нормального горения топлива в цилиндрах требуется огромное количество чистого воздуха – не менее 15 литров на каждый литр самого топлива. Тот воздух, который поступает к двигателю непригоден для обогащения топливной смеси без предварительной очистки, поскольку содержит слишком много твердых частиц и прочих загрязняющих компонентов. Попадая в двигатель, они становятся причиной быстрого износа поршневой группы, подшипников и коленвала. Воздушный фильтр в автомобиле качественно убирает посторонние примеси из воздуха, повышая стабильность работы двигателя и продлевая ему срок службы.</p>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Виды воздушных фильтров</center>
        <p>Все существующие на сегодняшний день воздушные фильтры можно разделить на следующие типы:</p>
          <li>цилиндрические</li>
          <li>бескаркасные</li>
          <li>панельные</li>
        <p>Кроме такой классификации, все они делятся и по степени фильтрации.</p>
          <li>«Абсолютные» фильтры – такие в состоянии задерживать практически 100% пыли и копоти из окружающего воздуха.</li>
          <li>Фильтр, улавливающий частицы пыли более 10 мкм.</li>
          <li>Фильтры, эффективно задерживающие все частицы, размером свыше 1 мкм.</li>
        <p>Отличаются воздушные автомобильные фильтры и материалом изготовления. Наиболее доступными являются элементы из картона, но они не отличаются высоким качеством очистки воздуха и практичностью. На следующее место можно поставить фильтрующие элементы из синтетических материалов – они задерживают на порядок больше грязи, но требуют строгого соблюдения интервала замены. Наиболее дорогостоящие воздушные фильтры изготавливаются из особой ткани, пропитанной специально разработанным маслом. Такие отличаются малым начальным сопротивлением и возможностью регенерации.</p>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Менять ли воздушный фильтр в авто?</center>
        <p>Чтобы понять, зачем менять воздушный фильтр в автомобиле можно озвучить следующий факт – в течении заявленного срока службы он должен пропускать не более 1% пыли. Если этот показатель становится выше, ресурс двигателя начинает уменьшаться. Основная часть воздушных не предусматривает возможность регенерации. Об этом говорят и сами производители, указывая на недопустимость различных «промываний». Тем не менее, многие автомобилисты по несколько лет могут использовать один и тот же воздушный фильтр, считая, что, промыв его, они полностью восстановили его свойства.</p>
        <p>На самом деле это далеко не так. После промывания он внешне будет выглядеть относительно чисто, что и вводит автомобилистов в заблуждение. На практике ресурс фильтрующего элемента ограничен чисто физически – после определенного срока использования он становится не в состоянии задерживать пыль, поскольку пористость материала резко повышается, и основная часть пыли и грязи из окружающего воздуха идет напрямую в двигатель. Практически все виды воздушных фильтров не подлежат восстановлению, поэтому его замена необходима каждые 10 000 км, а при эксплуатации автомобиля в сельской местности, на грунтовых дорогах делать это необходимо чаще.</p>
     </Div>
    </Group>
	</Panel>
);

AirFilter.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default AirFilter;
