import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Div,  platform, IOS, Group} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const AirFilter = props => (
	<Panel id={props.id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Поршни
      </PanelHeader>

    <Group>
     <Div>
      <center>Поршни</center>
        <p>Поршень двигателя — это деталь цилиндрической формы, совершающая возвратно-поступательное движение внутри цилиндра и служащая для превращения изменения давления газа, пара или жидкости в механическую работу, или наоборот — возвратно-поступательного движения в изменение давления.</p>
        <p>Изначально поршни для автомобильных двигателей внутреннего сгорания отливали из чугуна. С развитием технологий стали использовать алюминий для изготовления поршней, т.к. он давал следующие преимущества: рост оборотов и мощности, меньшие нагрузки на детали, лучшую теплоотдачу.</p>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Требования к поршням двигателя</center>
          <p><li>поршень, перемещаясь в цилиндре, позволяет расширяться сжатым газам, продукту горения топлива, и совершать механическую работу. Следовательно, он должен сопротивляться высокой температуре, давлению газов и надежно уплотнять канал цилиндра.</li>
          <li>представляя собой вместе с цилиндром и поршневыми кольцами линейный подшипник скольжения, он должен наилучшим образом отвечать требованиям пары трения с целью минимизировать механические потери и, как следствие, износ.</li>
          <li>испытывая нагрузки со стороны камеры сгорания и реакцию от шатуна, он должен выдерживать механическое воздействие.</li>
		  <li>совершая возвратно-поступательное движение с высокой скоростью, должен как можно меньше нагружать кривошипно-шатунный механизм инерционными силами.</li></p>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Основное назначение поршней в работе двигателя</center>
        <p>Топливо, сгорая в надпоршневом пространстве, выделяет огромное количество тепла в каждом цикле работы двигателя. Температура сгоревших газов достигает 2000 градусов. Только часть своей энергии они передадут движущимся деталям мотора, все остальное в виде тепла нагреет двигатель, а то, что останется, вместе с отработанными газами улетит в трубу. Следовательно, если мы не будем охлаждать поршень, он через некоторое время расплавится. Это важный момент для понимания условий работы поршневой группы.</p>
     </Div>
    </Group>
	</Panel>
);

AirFilter.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default AirFilter;
