import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Div,  platform, IOS, Group} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const WhatMG = props => (
	<Panel id={props.id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Что такое МКПП
      </PanelHeader>

    <Group>
     <Div>
      <center>Что такое МКПП?</center>
        <p>Механическая коробка передач (МКПП или МКП) — разновидность коробки передач, механизм, предназначенный для ступенчатого изменения передаточного отношения, в котором выбор передачи осуществляется оператором (водителем) вручную. Названа так, поскольку весь её основной функционал реализуется исключительно за счёт механических устройств, без применения гидравлических или электрических элементов.</p>
        <p>Коробка передач обеспечивает использование оптимального режима работы двигателя при движении в различных условиях. В механической коробке передач это осуществляется за счёт осуществляемого водителем переключения ступеней (передач), имеющих различное передаточное число.</p>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Преимущества МКПП:</center>
          <p><li>стоимость и масса «механики» относительно ниже, чем те же параметры у других типов коробок;</li>
          <li>КПД «механики» выше, нежели у коробок других типов;</li>
          <li>благодаря своей высокой надежности, «механика» имеет большой срок службы;</li>
          <li>механическая коробка предоставляет большой выбор стилей езды в различных условиях эксплуатации автомобиля, например, бездорожье, грязь, гололед;</li>
          <li>машину с «механикой» при необходимости можно буксировать куда угодно, не опасаясь неприятных последствий со стороны коробки;</li>
          <li>наличие именно ручной коробки обеспечивает возможность запуска автомобиля с «толкача» и допускает отсоединение трансмиссии от двигателя.</li></p>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Недостатки МКПП:</center>
          <p><li>в условиях длительной езды по городу или в пробках значительно больше утомляет водителя;</li>
          <li>нуждается в наличии у водителя определенных навыков управления автомобилем типа плавного перехода между передачами;</li>
          <li>сравнительно большое время смены передач, поскольку оно нуждается во временном отключении коробки от двигателя (выключении сцепления);</li>
          <li>относительно небольшой ресурс механизма сцепления.</li></p>
     </Div>
    </Group>
	</Panel>
);

WhatMG.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default WhatMG;
