import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, FormLayout, FormStatus, HeaderButton, platform, IOS, Cell, List, Group, Avatar} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Info from '@vkontakte/icons/dist/24/info';

const osname = platform();
const ServInfo = ({ go, nickname, fetchedUser, onClick, usersTop,  userAcc, name, updateUserNickname, id}) => (

	<Panel id={id}>
	  <PanelHeader
		left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
		</HeaderButton>}>
          Информация о сервисе
	  </PanelHeader>
	  
	<Group>
		<FormLayout>
        <FormStatus title="Уникальный">Мы постоянно работаем над функционалом, чтобы максимально соответствовать требованиям</FormStatus>
        <FormStatus title="Безопасный">Мы используем защищенное соединение</FormStatus>
		<FormStatus title="Мы дорожим своей репутацией">Вся статистика перед вами, комментарии о нас можно найти в группе ВКонтакте</FormStatus>
        </FormLayout>
	</Group>
	<Group>
      <List>
		<FormLayout>
        <FormStatus state="error">Сервис содержит в себе общую информацию об устройстве автомобиля и отлично подойдет для начинающих водителей.</FormStatus>
        </FormLayout>
        <Cell before={<Avatar style={{ background: 'var(--accent)' }} size={28}><Icon24Info fill="var(--white)" /></Avatar>}description="Версия 1.0"></Cell>
      </List>
    </Group>
    </Panel>
);

ServInfo.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
};

export default ServInfo;
