import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Div,  platform, IOS, Group} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const Collector = props => (
	<Panel id={props.id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Коллектор
      </PanelHeader>

    <Group>
     <Div>
      <center>Строение выпускного и впускного коллектора</center>
        <p>В сильно упрощенном виде конструкцию коллектора можно объяснить так: это одна труба, которая разделяется на 4 или более (а иногда и менее). Количество труб, что у впускного, что у выпускного коллектора напрямую зависит от числа цилиндров в двигателе. Например, у небезызвестной малолитражки «Ока» был 2-х цилиндровый мотор. У некоторых двигателей марки «Шкода» 3 цилиндра, в то время как ряд силовых агрегатов «Ауди» – 5-ти цилиндровые. Это если говорить о рядных моторах; у V-образных двигателей обычно от 6 до 12 цилиндров, однако у них 4 коллектора (по 2 на каждую сторону), да и форма несколько другая, нежели у рядных, хотя зависимость количества труб от кол-ва цилиндров сохраняется.</p>
        <p>Теперь подробнее о деталях, с которыми сопрягаются оба коллектора.</p>
          <li>Впускной является частью системы питания, и к нему подключен (у бензиновых моторов) карбюратор (сейчас такое уже почти не встречается) или дроссельный узел. У современных дизелей вместо всего этого стоит аккумуляторная топливная система, более известная как «Common Rail».</li>
          <li>Выпускной соединяется с приемной трубой (она же «штаны»), далее идет катализатор, резонатор и глушитель. На старых автомобилях катализатор отсутствует.</li>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Устройство впускного коллектора</center>
        <p>Предназначение впускного коллектора заключается в подведении топливно-воздушной смеси или только воздуха к цилиндрам. Почему или? Все зависит от особенностей конструкции системы питания. Впрочем, об этом ниже.</p>
		<p>Обычно эта деталь – металлическая, но иногда встречаются коллекторы из специального пластика, выдерживающего высокие температуры. Так делают для снижения стоимости и для облегчения веса мотора, а через это – и машины.</p>
		<p>Соединяется впускной коллектор разветвленной частью с головкой блока цилиндров (ГБЦ) через прокладку. При открывании впускных клапанов создается разряжение, с помощью которого топливно-воздушная смесь (или воздух) попадает в цилиндр, после чего клапана закрываются, и начинается такт сжатия.</p>
		<p>Несмотря на то, что ни воздух, ни смесь его с горючим не обладают высокой температурой, коллектор все равно нагревается от ГБЦ до 100°С. Поэтому если его делают из пластика, то берут специальный, высокотемпературный тип.</p>
		<p>Вернемся к вопросу с воздухом и топливно-воздушной смесью. Последняя подается через коллектор, если впрыск распределенный (т.е. форсунки инжектора установлены перед клапанами). Потом они открываются, и смесь топлива с воздухом попадает в цилиндр.</p>
		<p>Если же впрыск непосредственный, и топливо подается сразу в камеру сгорания, через коллектор проходит только воздух, а смешение происходит прямо в цилиндре.</p>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Устройство выпускного коллектора</center>
        <p>Задача выпускного коллектора – отведение выхлопных газов. На такте выпуска одноименные клапана открываются, и под воздействием движущегося наверх поршня газы попадают в коллектор.</p>
        <p>Он тоже подсоединен через прокладку разветвленной частью к ГБЦ, однако, посадочное место у него свое. Пройдя через коллектор, выхлопные газы попадают в приемную трубу, далее (на современных авто) в катализатор, где оседает значительная часть вредных веществ, потом в резонатор, снижающий громкость выхлопа, затем в глушитель, где звук исчезает полностью, и отводятся в атмосферу. У моторов с турбонаддувом газы после коллектора оказываются в специальном канале и крутят турбину, и только потом уходят в приемную трубу.</p>
		<p>У инжекторных двигателей и современных дизелей в конструкции выпускного коллектора предусмотрено место для установки лямбда-зонда – датчика, который контролирует количество различных газов в выхлопе.</p>
		<p>Основываясь в том числе и на показаниях лямба-зонда, электронный блок управления двигателем соответствующим образом дозирует подачу топлива, что приводит к возникновению взаимосвязи при работе коллекторов.</p>
     </Div>
    </Group>
	</Panel>
);

Collector.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Collector;
